import "../libs/ua-parser.min"
import { onCLS, onFID, onLCP, onFCP, onINP } from "../libs/web-vitals.js"
import {
    AnyObject,
    GtmElementOptions_Type,
    GtmElement_Type,
    HeartbeatParams_Type,
    Tags_Type,
    TrackerConfig_Type,
    UserInfo_Type,
} from "./types.dto"
import {
    ajax,
    filterObject,
    formatUUidTo32,
    getCookie,
    getLocalStorage,
    getQueryString,
    getUUid,
    isInvalidVal,
    millisecondToSecond,
    setCookie,
    setLocalStorage,
    transformObjKeyToLow,
} from "./util"

export class Tracker {
    gaTrackId = ""
    gameCode = ""
    activitycode = ""
    tbName = "web_user_action" // web_user_action
    userInfo: UserInfo_Type = {
        userid: "",
        phone: "",
    }
    device = ""
    version = "1.4.1"
    clientTag = "" // 
    heartBeatUrl = "https://apm.efun.com/apm/heartbeat"
    abtName = "origin" // aborigin-
    efePreFix = "_efe_" // efe
    CLIENT_ID_COOKIE_EXPIRE = 3 * 30 * 24 * 60 * 60 * 1000 // idcookie3
    SESSION_ID_EXPIRE = 30 * 60 * 1000 // session_idcookie30
    SESSION_ID_KEY = "" // idkey
    CLIENT_ID_KEY = "" // idkey
    CLIENT_TAG_KEY = "" // key
    PV_COUNT_KEY = ""
    isInsertGtagJs = true // gtag.js
    gtmElementOptions: GtmElementOptions_Type = {
        options: {},
        list: [],
    }
    vitalSet: AnyObject = {} // 
    isHeartBeat = true // 
    isSendGA = true // ga
    startTime = new Date().getTime()
    observer: IntersectionObserver

    init(config: TrackerConfig_Type) {
        const {
            gaId,
            gameCode,
            activityCode,
            abtName,
            sessionTimes,
            activityTimes,
            gtmElementOptions,
            isInsertGtagJs,
        } = config
        if (!gaId || !gameCode || !activityCode) {
            throw new Error("gaId and gameCode and activityCode is required")
        }
        const efeSuffix = gameCode + "_" + activityCode

        this.gaTrackId = gaId
        this.activitycode = activityCode
        this.gameCode = gameCode
        this.isInsertGtagJs = isInvalidVal(isInsertGtagJs)
            ? true
            : isInsertGtagJs

        this.abtName = abtName || window.abTestName || this.abtName
        this.SESSION_ID_EXPIRE = isInvalidVal(sessionTimes)
            ? this.SESSION_ID_EXPIRE
            : sessionTimes
        this.CLIENT_ID_COOKIE_EXPIRE = activityTimes
            ? activityTimes
            : this.CLIENT_ID_COOKIE_EXPIRE
        this.SESSION_ID_KEY = this.efePreFix + "sid_" + efeSuffix
        this.CLIENT_ID_KEY = this.efePreFix + "cid_" + efeSuffix
        this.CLIENT_TAG_KEY = this.efePreFix + "tag_" + efeSuffix
        this.PV_COUNT_KEY = this.efePreFix + "pv_" + efeSuffix
        this.gtmElementOptions = Object.assign(
            this.gtmElementOptions,
            gtmElementOptions
        )
        this.clientTag = this.getClientTag()
        this.initHtmlHead()
        this.bindEvent()
        const cidKey = "device"
        this.device = getLocalStorage(cidKey)
        if (!this.device) {
            this.device = getUUid()
            setLocalStorage(cidKey, this.device)
        }

        // 
        this.initVitalsCallback()
        // pv
        this.sendPageViewEvent()
        // idcookie
        this.resetClientIdExpire()
    }

    /**
     * @description: head
     * @return {*}
     */
    initHtmlHead() {
        const head = document.getElementsByTagName("head")[0]
        if (this.isInsertGtagJs) {
            const scriptSrcGaTag = document.createElement("script")
            const scriptAnalyticsTag = document.createElement("script")
            scriptAnalyticsTag.async = true
            scriptSrcGaTag.async = true
            scriptSrcGaTag.src =
                "https://www.googletagmanager.com/gtag/js?id=" + this.gaTrackId
            scriptAnalyticsTag.src =
                "https://www.google-analytics.com/analytics.js"
            head.appendChild(scriptSrcGaTag)
        }
        const scriptTag = document.createElement("script")
        scriptTag.innerHTML = `
        window.dataLayer = window.dataLayer || []
        function gtag() {
            dataLayer.push(arguments)
        }
        gtag('js', new Date())
        gtag('config', '${this.gaTrackId}')
    `
        head.appendChild(scriptTag)
    }

    bindEvent() {
        /**pageView */
        window.addEventListener("load", () => {
            this.initGTMVisibleElement(this.gtmElementOptions)
            this.sendPerformanceEvent()
        })

        // 
        let firstHidden = true
        document.addEventListener("visibilitychange", () => {
            if (document.visibilityState === "hidden" && firstHidden) {
                this.sendPerformanceEvent()
                firstHidden = false
            }
        })

        /** */
        window.addEventListener("beforeunload", (e) => {
            const outOption = {
                stop_time: this.getPerformanceTime("stop"),
            }
            const stopOption = this.getHeartBeatData(
                "",
                "",
                "",
                {
                    event_type: "auto",
                },
                outOption
            )
            this.heartbeat([stopOption])
        })
    }

    /**
     * @description: gtm
     * @param {GtmElementOptions_Type} gtmElementOptions
     * @return {*}
     */
    initGTMVisibleElement(gtmElementOptions: GtmElementOptions_Type) {
        if (
            !gtmElementOptions ||
            !gtmElementOptions.list ||
            !gtmElementOptions.list.length
        ) {
            return
        }
        const list = gtmElementOptions.list
        const options = Object.assign(
            {
                root: null,
                rootMargin: "0px",
                threshold: [0.5, 0.75, 1],
            },
            gtmElementOptions.options
        )

        const that = this

        that.observer = new IntersectionObserver((e) => {
            e.forEach((item) => {
                const { isIntersecting, target } = item

                if (isIntersecting) {
                    // 
                    that.observer.unobserve(target)
                    const {
                        action,
                        type,
                        label,
                        // @ts-ignore
                    } = target.trackerInfo as GtmElement_Type
                    const params = that.getHeartBeatData(type, action, label, {
                        event_type: "view",
                    })
                    const paramsArr = [params]

                    that.heartbeat(paramsArr)
                }
            })
        }, options)

        list.forEach((item) => {
            const { action, type, label, selector } = item
            const dom = document.querySelector(selector)
            if (dom) {
                // 
                // @ts-ignore
                dom.trackerInfo = {
                    action,
                    type,
                    label,
                }
                that.observer.observe(dom)
            }
        })
    }

    /**
     * @description: vitals
     * @return {*}
     */
    initVitalsCallback() {
        onLCP(onVital, {
            reportAllChanges: true,
        })
        onFCP(onVital)
        onCLS(onVital)
        onFID(onVital)
        onINP(onVital)
        const that = this
        function onVital(e: { name: any; id: any; delta: any }) {
            const { name, id, delta } = e
            // idkeyid

            that.vitalSet[id] = {
                name: name.toLowerCase(),
                delta:
                    name !== "CLS"
                        ? Math.round(delta)
                        : Math.round(delta * 100) / 100,
            }

            // 
            if (["FID"].includes(name)) {
                that.sendPerformanceEvent()
            }
        }
    }

    /**
     * @description: idcookie
     * @param {number} time (ms)3
     * @return {*}
     */
    resetClientIdExpire(time?: number) {
        const key = this.CLIENT_ID_KEY
        const value = getCookie(key)
        const newValue = value || this.device || getUUid()
        // cidcid
        if (!value) {
            setCookie(this.CLIENT_TAG_KEY, "new", this.CLIENT_ID_COOKIE_EXPIRE)
        }

        setCookie(key, newValue, time || this.CLIENT_ID_COOKIE_EXPIRE)
    }

    /**
     * @description: session_idsession_id
     * 1. session_id(30)
     * 2. 12session_id
     * 3. session_id
     * @return {string} session_id
     */
    resetSessionIdExpire() {
        let sessionId = ""
        const isTwenty = this.isTwentyFour()
        const isAdvChange = this.isAdvChange()
        // 12session_id
        if (isTwenty || isAdvChange) {
            sessionId = getUUid()
            this.setPvCountInSession(0)
        } else {
            const sid = getCookie(this.SESSION_ID_KEY)
            // 
            if (!sid) {
                // 
                this.tagOldClient()
            }
            sessionId = sid || getUUid()
        }
        setCookie(this.SESSION_ID_KEY, sessionId, this.SESSION_ID_EXPIRE)
        return sessionId
    }

    /**
     * @description: 
     */
    tagOldClient() {
        const cTag = getCookie(this.CLIENT_TAG_KEY)
        if (cTag && cTag === "new") {
            setCookie(this.CLIENT_TAG_KEY, "old", this.CLIENT_ID_COOKIE_EXPIRE)
        }
    }

    /**
     * @description: pv
     * @return {*}
     */
    sendPageViewEvent() {
        const params = [
            this.getHeartBeatData("pageView", "pageView", "", {
                event_type: "auto",
            }),
        ]
        const pvEventCount = this.getPvCountInSession()
        this.setPvCountInSession(pvEventCount + 1)
        // sessionIdsessionIdpvsessionId
        if (pvEventCount === 1) {
            // 
            params.push(
                this.getHeartBeatData("", "", "", {
                    event_type: "action",
                })
            )
        }

        this.heartbeat(params)
    }

    /**
     * @description: 
     * @return {*}
     */
    sendPerformanceEvent() {
        // 
        const allTimeObj = this.getPerformanceTime("all") as AnyObject
        const performanceObj = filterObject(allTimeObj, (key, val) => {
            return key !== "stop_time"
        })
        for (const key in this.vitalSet) {
            const { name, delta } = this.vitalSet[key]
            performanceObj[`${name}_${name !== "cls" ? "time" : "score"}`] =
                delta
        }

        const loadOption = this.getHeartBeatData(
            "",
            "",
            "",
            {
                event_type: "auto",
            },
            performanceObj
        )

        this.heartbeat([loadOption])
    }

    /**
     * @description: 
     * @return {*}
     */
    sendTrackEvent(
        type: string,
        action: string = "",
        label: string,
        callback: Function
    ) {
        // google
        this.isSendGA &&
            this.sendToGoogleAnalytics(type, action, label, callback)
        // 
        const params = this.getHeartBeatData(type, action, label, {
            event_type: "action",
        })

        const paramsArr = [params]

        this.heartbeat(paramsArr)
    }

    /**
     * @description: GoogleAnalytics
     * @return {*}
     */
    sendToGoogleAnalytics(
        type: string,
        action: string,
        label: string,
        callback: Function
    ) {
        const gtag = window.gtag
        if (gtag) {
            gtag("event", action, {
                event_category: type,
                event_label: label || action,
                event_callback: () => {
                    this.gaCallback(callback)
                },
            })
        } else {
            this.gaCallback(callback)
        }
    }

    gaCallback(callback: Function) {
        if (callback && typeof callback !== "function") {
            location.href = callback
        } else {
            callback && callback()
        }
    }

    /**
     * @description: 
     * @return {*}
     */
    getHeartBeatData(
        action_category: string,
        action: string,
        label: string,
        tagsOtherInfo: Tags_Type = {},
        fieldsOtherInfo = {}
    ): HeartbeatParams_Type {
        // tags
        const baseTags = this.getBaseTags(action)
        const tags = {
            ...baseTags,
            action_category,
            label: label || action,
            ...tagsOtherInfo,
        }

        // fields
        const baseFields = this.getBaseFields()
        const fields = {
            ...baseFields,
            ...fieldsOtherInfo,
        }

        const newTags = transformObjKeyToLow(tags)
        // keyfields
        const newFields = filterObject(
            transformObjKeyToLow(fields),
            (key, val) => !isInvalidVal(val)
        )

        // urlurl
        if (!newFields.url) {
            newFields.url = window.location.href
        }

        // session_idsession_id
        if (!newFields.session_id) {
            newFields.session_id = this.resetSessionIdExpire()
        }

        return {
            tb: this.tbName,
            logtime: millisecondToSecond(new Date().getTime()),
            msgid: formatUUidTo32(getUUid()),
            tags: newTags,
            fields: newFields,
        }
    }

    /**
     * @description: 
     * @param {string} type
     * @return {*}
     */
    getPerformanceTime(type: string) {
        const performance = window.performance

        if (!performance) {
            throw new Error("performance not supported")
        }
        if (performance.getEntries) {
            return this.getTimesByNavigationTiming(type)
        }
        // 
        if (performance.timing) {
            return this.getTimesByTiming(type)
        }
    }

    /**
     * @description: PerformanceNavigationTiming
     * @param {*} type
     * @return {*}
     */
    getTimesByNavigationTiming(type: string) {
        const timing =
            performance.getEntries()[0] as PerformanceNavigationTiming

        let time = 0

        const typeMap = {
            white: () => {
                time = timing.responseEnd - timing.requestStart
            },
            load: () => {
                time = timing.loadEventStart - timing.startTime
            },
            stop: () => {
                time = performance.now()
            },
            redirect: () => {
                time = timing.redirectEnd - timing.redirectStart
            },
            dns: () => {
                time = timing.domainLookupEnd - timing.domainLookupStart
            },
            tcp: () => {
                time = timing.connectEnd - timing.connectStart
            },
            request: () => {
                time = timing.responseStart - timing.requestStart
            },
            response: () => {
                time = timing.responseEnd - timing.responseStart
            },
            ttfb: () => {
                time = timing.responseStart
            },
            fcp: () => {
                time = 0
            },
            lcp: () => {
                time = 0
            },
            fid: () => {
                time = 0
            },
            inp: () => {
                time = 0
            },
            cls: () => {
                time = 0
            },
        } as AnyObject

        // 
        if (type === "all") {
            const allTimeObj = {} as AnyObject
            for (let key in typeMap) {
                typeMap[key]()
                const keyName = key !== "cls" ? `${key}_time` : "cls_score"
                allTimeObj[keyName] = Math.round(time)
            }

            return allTimeObj
        }

        typeMap[type]()

        return Math.round(time)
    }

    /**
     * @description: PerformanceTiming
     * @param {*} type
     * @return {*}
     */
    getTimesByTiming(type: string) {
        const timing = performance.timing
        let time = 0

        const typeMap = {
            white: () => {
                time = timing.responseEnd - timing.requestStart
            },
            load: () => {
                time = timing.loadEventStart - timing.navigationStart
            },
            stop: () => {
                time = performance.now()
            },
            redirect: () => {
                time = timing.redirectEnd - timing.redirectStart
            },
            dns: () => {
                time = timing.domainLookupEnd - timing.domainLookupStart
            },
            tcp: () => {
                time = timing.connectEnd - timing.connectStart
            },
            request: () => {
                time = timing.responseStart - timing.requestStart
            },
            response: () => {
                time = timing.responseEnd - timing.responseStart
            },
            ttfb: () => {
                time = timing.responseStart - timing.navigationStart
            },
            fcp: () => {
                time = 0
            },
            lcp: () => {
                time = 0
            },
            fid: () => {
                time = 0
            },
            inp: () => {
                time = 0
            },
            cls: () => {
                time = 0
            },
        } as AnyObject

        if (type === "all") {
            const allTimeObj = {} as AnyObject
            for (let key in typeMap) {
                const keyName = key !== "cls" ? `${key}_time` : "cls_score"
                allTimeObj[keyName] = typeMap[key]()
            }
            return allTimeObj
        }

        typeMap[type]()

        return Math.ceil(time)
    }

    /**
     * @description: tags
     * @param {string} action
     * @return {*}
     */
    getBaseTags(action?: string): Tags_Type {
        const titleDom = document.querySelector("head title")
        const web_title = titleDom.innerHTML
        const advertInfo = this.getAdvertInfo()
        const userAgentInfo = this.getUserAgentInfo()

        return {
            page_id: this.gaTrackId,
            gamecode: this.gameCode,
            action: action || "",
            activitycode: this.activitycode,
            web_title,
            event_type: "",
            abt_name: this.abtName || "",
            client_tag: this.clientTag,
            ...advertInfo,
            ...userAgentInfo,
        }
    }

    /**
     * @description: fields
     * @return {*}
     */
    getBaseFields() {
        const { userid, phone } = this.getUserInfo()

        return {
            url: window.location.href,
            client_id: this.device,
            phone: this.userInfo.phone || phone,
            session_id: this.resetSessionIdExpire(),
            userid: this.userInfo.userid || userid,
        }
    }

    /**
     * @description: 
     * @return {*}
     */
    getUserInfo(): UserInfo_Type {
        return {
            timestamp:
                getCookie("timestamp") || getCookie("loginTimestamp") || "",
            userid: getCookie("userId") || getCookie("efunUserid") || "",
            phone: getCookie("phone") || "",
        }
    }

    /**
     * @description: 
     */
    getAdvertInfo() {
        const advertiser = getQueryString("utm_source") || ""
        const asset_id = getQueryString("asset_id") || ""
        const campaign_name = getQueryString("campaign_name") || ""
        const adgroup_name = getQueryString("adgroup_name") || ""
        const ad_name = getQueryString("ad_name") || ""
        const custom = getQueryString("utm_campaign") || ""

        return {
            advertiser,
            asset_id,
            campaign_name,
            adgroup_name,
            ad_name,
            custom,
        }
    }

    /**
     * @description: UserAgent
     */
    getUserAgentInfo() {
        // @ts-ignore
        if (!window.UAParser) {
            throw new Error("please introduce ua-parser.min.js")
        }

        const {
            browser,
            device,
            engine,
            os,
            // @ts-ignore
        } = UAParser()
        const plat =
            os.name === "iOS" || os.name === "Mac OS" ? "ios" : "android"

        let device_model = device.model
            ? device.model + "#" + device.vendor + "#" + device.type
            : "pc"
        // undefined(not set)
        device_model.includes("undefined") &&
            (device_model = device_model.replace(/undefined/i, "(not set)"))
        return {
            browser: browser.name + "#" + browser.version,
            device_model,
            browser_engine: engine.name + "#" + engine.version,
            os: os.name + "#" + os.version,
            plat,
        }
    }

    /**
     * @description: 
     * @return {*}
     */
    getClientTag(): "old" | "new" {
        const cid = getCookie(this.CLIENT_ID_KEY)
        const cTag = getCookie(this.CLIENT_TAG_KEY)

        if (cid && cTag !== "new") {
            return "old"
        }

        if (!cid || (cid && cTag === "new")) {
            return "new"
        }

        return "old"
        // return !!getCookie(this.CLIENT_ID_KEY) ? "old" : "new"
    }

    /**
     * @description: idcookie
     * @param {number} times
     */
    setActivityTimes(time: number) {
        this.CLIENT_ID_COOKIE_EXPIRE = isInvalidVal(time)
            ? time
            : this.CLIENT_ID_COOKIE_EXPIRE
        this.resetClientIdExpire()
    }

    /**
     * @description: 
     * @param {number} phone
     */
    setUserPhone(phone: number | string) {
        if (isInvalidVal(phone)) {
            return
        }

        if (typeof phone === "string") {
            this.userInfo.phone = phone.trim()
        } else if (typeof phone === "number") {
            this.userInfo.phone = phone.toString()
        }
    }

    /**
     * @description: pv
     * @param {number} count
     * @return {*}
     */
    setPvCountInSession(count: number) {
        setCookie(this.PV_COUNT_KEY, count, this.SESSION_ID_EXPIRE)
    }

    /**
     * @description: pv
     */
    getPvCountInSession() {
        return Number(getCookie(this.PV_COUNT_KEY) || 0)
    }

    /**
     * @description: 12
     * @return {*}
     */
    isTwentyFour() {
        const date = new Date()
        const daily = `${date.getMonth() + 1}-${date.getDate()}`
        // 
        const prevDaily = getLocalStorage(`${this.efePreFix}prev_daily`)

        if (isInvalidVal(prevDaily) || prevDaily != daily) {
            setLocalStorage(`${this.efePreFix}prev_daily`, daily)
            return true
        }

        return false
    }

    /**
     * @description: 
     * @return {*}
     */
    isAdvChange() {
        let res = false

        const curAdvInfo = {
            utm_source: getQueryString("utm_source"),
            utm_medium: getQueryString("utm_medium"),
            utm_campaign: getQueryString("utm_campaign"),
            utm_term: getQueryString("utm_term"),
            utm_content: getQueryString("utm_content"),
        } as AnyObject

        const prevAdvInfo = {
            utm_source:
                getLocalStorage(
                    `${this.gameCode}_${this.activitycode}_utm_source`
                ) || "",
            utm_medium:
                getLocalStorage(
                    `${this.gameCode}_${this.activitycode}_utm_medium`
                ) || "",
            utm_campaign:
                getLocalStorage(
                    `${this.gameCode}_${this.activitycode}_utm_campaign`
                ) || "",
            utm_term:
                getLocalStorage(
                    `${this.gameCode}_${this.activitycode}_utm_term`
                ) || "",
            utm_content:
                getLocalStorage(
                    `${this.gameCode}_${this.activitycode}_utm_content`
                ) || "",
        } as AnyObject

        for (const key in curAdvInfo) {
            if (curAdvInfo[key] !== prevAdvInfo[key]) {
                res = true
                setLocalStorage(
                    `${this.gameCode}_${this.activitycode}_${key}`,
                    curAdvInfo[key]
                )
            }
        }

        return res
    }

    /**
     * @description: 
     * @param {any} datas
     * @return {*}
     */
    heartbeat(datas: HeartbeatParams_Type[]) {
        if (!this.isHeartBeat || !datas.length) {
            return Promise.resolve("bind send")
        }
        if (!this.gameCode || !this.activitycode) {
            throw new Error("gaId and gameCode and activityCode is required")
        }
        return new Promise((resolve, reject) => {
            if (!(datas instanceof Array)) {
                return
            }
            const url = this.heartBeatUrl
            const heads = {
                "content-type": "application/json",
                game: this.gameCode,
                device: this.device,
            }
            const data = {
                version: this.version,
                datas,
            }
            const params = {
                url,
                method: "post",
                heads,
                data,
            }
            ajax(params)
                .then((res) => {
                    resolve(res)
                })
                .catch((err) => {
                    reject(err)
                })
        })
    }
}
